import {createRouter, createWebHistory} from 'vue-router';

const routes = [
    {path: '/', name: 'main', component: () => import('./pages/MainDataComponent.vue')},
    {path: '/login', name: 'login', component: () => import('./pages/LoginComponent.vue')},
    {path: '/analitics', name: 'analitics', component: () => import('./pages/AnaliticComponent.vue')},
    {path: '/library', name: 'library', component: () => import('./pages/LibraryComponent.vue')},
    {path: '/plan-fact', name: 'plan-fact', component: () => import('./pages/PlanFactComponent.vue')},
    {path: '/weekends', name: 'weekends', component: () => import('./pages/WeekendsComponent.vue')},
    {path: '/calculations', name: 'calculations', component: () => import('./pages/CalculationsComponent.vue')},
    {path: '/:pathMatch(.*)*', name: 'error', component: () => import('./pages/ErrorComponent.vue')}
];

const router = createRouter({
    history: createWebHistory(),
    routes,
})

export default router;